<template>
  <div id="home">
    <topnav @showlog="showlog" :degree='degree'></topnav>
    <div v-if="!logshow" class="mongolia" @click="showlog"></div>
    <log-box v-show="!logshow"></log-box>
    <div class="router">
      <keep-alive exclude="ins">
        <router-view></router-view>
      </keep-alive>
    </div>
  </div>
</template>

<script>
import topnav from "@/components/topNav.vue";
import logbox from "@/components/login.vue"; // iteration 2 function, hidden in iteration 1


export default {
  data() {
    return {
      logshow: true,
      menushow: false,
      degree: 0,
    };
  },
  methods: {
    // show login box
    showlog() {
      this.logshow = !this.logshow;
      this.menushow = false;
    },
  },
  // register components
  components: {
    'topnav': topnav,
    "log-box": logbox,
  },
};
</script>
