import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Enter from '../views/Enter.vue'
import store from '@/store/index.js'

Vue.use(VueRouter)

const routes = [
{
  path: '/',
  name: 'Enter',
  component: Enter
},
{
  path: '/home',
  name: 'Home',
  component: Home,
  children: [{
    path: '/',
    name: 'website',
    component: () => import(/* webpackChunkName: "website" */ '../views/home/websit.vue')
  }, {
    path: '/home/vis',
    name: 'vis',
    component: () => import(/* webpackChunkName: "vis" */ '../views/home/vis.vue')
  }, {
    path: '/home/ins',
    name: 'ins',
    component: () => import(/* webpackChunkName: "ins" */ '../views/home/ins.vue')
  }, {
    path: '/home/meal',
    name: 'meal',
    component: () => import(/* webpackChunkName: "meal" */ '../views/home/meal.vue')
  }, {
    path: '/home/exc',
    name: 'exc',
    component: () => import(/* webpackChunkName: "exc" */ '../views/home/exc.vue')
  }, {
    path: '/home/mental',
    name: 'mental',
    component: () => import(/* webpackChunkName: "mental" */ '../views/home/mental.vue')
  }]
},
{
  path: '/c',
  name: 'Caregiver',
  component: () => import(/* webpackChunkName: "caregiver" */ '../views/caregiver.vue')
},
  // {
  //   // path: '/about',
  //   // name: 'About',
  //   // // route level code-splitting
  //   // // this generates a separate chunk (about.[hash].js) for this route
  //   // // which is lazy-loaded when the route is visited.
  //   // component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  // }
]

const router = new VueRouter({
  scrollBehavior(to, from, saveTop) {
    if (saveTop) {
      return saveTop;
    } else {
      return { x: 0, y: 0 }
    }
  },
  routes
})

// if user did not log in, will redirect to he enter page
router.beforeEach((to, from, next) => {
  console.log(store.state.logined)
  if (to.name !== 'Enter' && !store.state.logined) next({ name: 'Enter' })
  if (to.name == 'Enter' && store.state.logined) next({ name: 'Home' })
  next()
})

export default router
